.work{
    width: 280px;
    background-color: #f7f7f7;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: center;
}

.watchWork{
    width: 90px;
    height: 50px;
    background-color: #c1c1e2;
}


.nameProject{
    background-color: #d0cbcb;
    width: 100%;
    text-align: start;
}
.descriptionProject{
    background-color: #c4b9b9;
    width: 100%;
    text-align: start;
}
.workPhoto{
    width: 280px;
    height: 280px;
    background-color: #eeeef5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}